* {
	box-sizing: border-box;
}
html,
body,
#root {
	font-family: 'Merriweather Sans', sans-serif;
	font-weight: 500;
	border: 0;
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	width: 100vw;
	height: 100vh;
}
#root {
	display: flex;
	flex-flow: column;
	align-items: center;
	justify-content: center;
}
